//
// Buttons Base
//

// Button
.btn {
    --#{$prefix}btn-color: var(--kt-body-color);
    --#{$prefix}btn-bg: transparent;
    --#{$prefix}btn-border-color: transparent;

    // Reset outline
    outline: none !important;

    // Reset focus shadow
    &:not(.btn-shadow):not(.shadow):not(.shadow-sm):not(.shadow-lg):not(.shadow-xs) {
        box-shadow: none !important;
    }

    // Remove border
    &:not(.btn-outline):not(.btn-dashed):not(.border-hover):not(.border-active):not(.btn-flush):not(.btn-icon) {
        border: 0;
        padding: calc(#{$btn-padding-y} + #{$btn-border-width})
            calc(#{$btn-padding-x} + #{$btn-border-width});

        &.btn-lg {
            padding: calc(#{$btn-padding-y-lg} + #{$btn-border-width})
                calc(#{$btn-padding-x-lg} + #{$btn-border-width});
        }

        &.btn-sm {
            padding: calc(#{$btn-padding-y-sm} + #{$btn-border-width})
                calc(#{$btn-padding-x-sm} + #{$btn-border-width});
        }
    }

    // Link
    &.btn-link {
        border: 0;
        border-radius: 0;
        padding-left: 0 !important;
        padding-right: 0 !important;
        text-decoration: none;
        font-weight: $btn-font-weight;
    }

    // Outline
    &.btn-outline:not(.btn-outline-dashed) {
        border: 1px solid var(--kt-input-border-color);
    }

    // Outline dashed
    &.btn-outline-dashed {
        border: 1px dashed var(--kt-input-border-color);
    }

    // Flush
    &.btn-flush {
        @include button-reset();
    }

    // Flex
    &.btn-flex {
        display: inline-flex;
        align-items: center;
    }

    // Align start
    &.btn-trim-start {
        justify-content: flex-start !important;
        padding-left: 0 !important;
    }

    // Align start
    &.btn-trim-end {
        justify-content: flex-end !important;
        padding-right: 0 !important;
    }
}

// Icons
.btn {
    // Font icon
    i {
        display: inline-flex;
        font-size: $font-size-base;
        padding-right: 0.35rem;
        vertical-align: middle;
        line-height: 0;
    }

    // Svg icon
    .svg-icon {
        flex-shrink: 0;
        line-height: 0;
        margin-right: 0.5rem;
    }

    // Icon only button
    &.btn-icon {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        height: $input-height;
        width: $input-height;

        // Remove border
        &:not(.btn-outline):not(.btn-dashed):not(.border-hover):not(.border-active):not(.btn-flush) {
            border: 0;
        }

        // Sizes
        &.btn-sm {
            height: $input-height-sm;
            width: $input-height-sm;
        }

        &.btn-lg {
            height: $input-height-lg;
            width: $input-height-lg;
        }

        &.btn-circle {
            border-radius: 50%;
        }

        i,
        .svg-icon {
            padding: 0;
            margin: 0;
            line-height: 1;
        }
    }
}
