// Options
//
// Quickly modify global styling by enabling or disabling optional features.
$enable-rounded: true !default;
$enable-shadows: true !default;
$enable-negative-margins: true !default;
$enable-smooth-scroll: false !default;

// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.
$spacer: 1rem !default;

$spacers: (
    0: 0,
    1: (
        $spacer * 0.25,
    ),
    // 3.5px
    2:
        (
            $spacer * 0.5,
        ),
    // 7px;
    3:
        (
            $spacer * 0.75,
        ),
    // 10.5px
    4:
        (
            $spacer * 1,
        ),
    // 14px
    5:
        (
            $spacer * 1.25,
        ),
    // 17.5px
    6:
        (
            $spacer * 1.5,
        ),
    // 21px
    7:
        (
            $spacer * 1.75,
        ),
    // 24.5px
    8:
        (
            $spacer * 2,
        ),
    // 28px
    9:
        (
            $spacer * 2.25,
        ),
    // 31.5px
    10:
        (
            $spacer * 2.5,
        ),
    // 35px
    11:
        (
            $spacer * 2.75,
        ),
    // 38.5px
    12:
        (
            $spacer * 3,
        ),
    // 42px
    13:
        (
            $spacer * 3.25,
        ),
    // 45.5px
    14:
        (
            $spacer * 3.5,
        ),
    // 49px
    15:
        (
            $spacer * 3.75,
        ),
    // 52.5px
    16:
        (
            $spacer * 4,
        ),
    // 55px
    17:
        (
            $spacer * 4.25,
        ),
    // 58.5px
    18:
        (
            $spacer * 4.5,
        ),
    // 62px
    19:
        (
            $spacer * 4.75,
        ),
    // 65.5px
    20:
        (
            $spacer * 5,
        ),
    // 69px
);

// Position
//
// Define the edge positioning anchors of the position utilities.
$position-values: (
    0: 0,
    10: 10%,
    20: 20%,
    25: 25%,
    50: 50%,
    75: 75%,
    80: 80%,
    90: 90%,
    100: 100%,
) !default;

// Body
//
// Settings for the `<body>` element.
$body-bg: $white !default;
$body-bg-rgb: to-rgb($body-bg) !default;
$body-bg-dark: #1e1e2d !default;
$body-bg-rgb-dark: to-rgb($body-bg-dark) !default;
$body-color: $gray-900 !default;
$body-color-dark: $gray-900-dark !default;
$body-text-align: null !default;

// Links
//
// Style anchor elements.
$link-color: $primary !default;
$link-color-dark: $primary !default;
$link-decoration: none !default;
$link-shade-percentage: 20% !default;
$link-hover-color: $primary-active !default;
$link-hover-color-dark: $primary-active !default;
$link-hover-decoration: none !default;

// Paragraphs
//
// Style p element.
$paragraph-margin-bottom: 1rem !default;

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.
// scss-docs-start grid-breakpoints
$grid-breakpoints: (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
    xxl: 1400px,
) !default;
// scss-docs-end grid-breakpoints

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.
$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1140px,
    xxl: 1320px,
) !default;

// Components
//
// Define common padding and border radius sizes and more.
$border-width: 1px !default;
$border-color: $gray-200 !default;
$border-color-dark: $gray-200-dark !default;
